<template>
<transition name="slide-fade">
  <div>
    <div id="qrpopup" class="text-center" v-if="popup">
       <input type="text" id="editNameBox" v-model="editNameBox"/><br>
       <input type="tel" id="editNumberBox" v-model="editNumberBox"/><br>
       <button class="boton" @click="goDelete()">eliminar</button>
       <button class="boton" @click="goUpdate()">guardar</button>
       <button class="boton" @click="popup=false">cerrar</button>
    </div>
    <div class="container glass mt-4 mb-4">

    <div class="row">

      <div class="col-12">
        <div class="data">
          <router-link to='/' class="home">⌂</router-link>
          <p>
            <h1>Premios</h1>
               <!--popup edición-->


              <div class="box">
          		<form id="form">
            		<table border="0" style="border: 0;" >
            			<tr>
            				<td>Premio</td>
            				<td><input type="text" v-model="txtName" class="form-control" id="txtName" placeholder="Nombre"></td>
            			</tr>
            			<tr>
            				<td>Cantidad</td>
            				<td><input type="tel" v-model="txtNumber" class="form-control" id="txtNumber" placeholder="Cantidad"></td>
            			</tr>
            		</table>
                  <br/>

          		</form>
              </div>

              <div style="text-align: center">
          		    <button class="btn" @click="goInsert()" id="fixed">Agregar</button>
              </div>
              <hr/>
              <div id="tblDiv">
                <table class="table table-striped" align="center" id="t01" border="1">
                  <thead>
                    <tr>
                      <th scope="col">ID</th>
                      <th scope="col">Premio</th>
                      <th scope="col">cantidad</th>
                      <th scope="col">editar</th>
                    </tr>
                  </thead>
                  <tr v-for="(premio,index) in premios" :id="index">
                    <td >{{premio.id}}</td>
                    <td>{{premio.name}}</td>
                    <td>{{premio.number}}</td>
                    <td align="center" @click="goPopup(premio.id,premio.name,premio.number)">&#9998;</td>
                  </tr>
                </table>
              </div>
          </p>
        </div>

      </div>
    </div>

    </div>

  </div>
</transition>

</template>

<script>
import axios from 'axios';
export default {
  name: 'SetUp',
  data(){
    return{
      db:null,
      popup:false,
      txtName:'',
      txtNumber:0,
      editNameBox:null,
      editNumberBox:null,
      editIdBox:null,
      premios:[]
    }
  },
  methods:{
    goInsert() {
        if((this.txtName!='')&&(!isNaN(this.txtNumber))){
          let self = this;
          let sql = 'INSERT INTO premios (name,number) VALUES ("' +this.txtName+'","'+this.txtNumber+'")';
          console.log(sql);
          this.db.transaction(function (tx) {
              tx.executeSql(sql);
              self.listData();
              self.txtName='';
              self.txtNumber=0;
          }, function (err) {
              console.log(err.message);
          },console.log('ok'));
        }else{
          alert('Debes ingresar los datos');
        }
    },
    goDelete() {
      let self = this;
      let sql = 'DELETE FROM premios WHERE id = ' + this.editIdBox;
      this.db.transaction(function (tx) {
          tx.executeSql(sql);
          self.listData();
          self.popup=false;
          self.editIdBox = null;
          self.editNameBox = null;
        	self.editNumberBox = null;
      }, function (err) {
          console.log(err.message);
      },console.log('ok'));
    },
    goUpdate() {
      let self = this;
      let sql = `UPDATE premios set name='${self.editNameBox}', number='${self.editNumberBox}' WHERE id = ${self.editIdBox}`;
      this.db.transaction(function (tx) {
          console.log(sql);
          tx.executeSql(sql);
          self.listData();
          self.popup=false;
          self.editIdBox = null;
          self.editNameBox = null;
        	self.editNumberBox = null;
      }, function (err) {
          console.log(err.message);
      },console.log('ok'));
    },
    goPopup(row,rowname,rownum) {
      console.log('popup');
      let currentRow=row;
    	this.popup=true;
      this.editIdBox = row;
      this.editNameBox = rowname;
    	this.editNumberBox = rownum;
    },
    listData(){
      console.log('listData');
      this.db = openDatabase(this.$BBDD, '1.0', 'erres', 2 * 1024 * 1024);
      let sql = "SELECT * FROM premios";
      let self = this;
      this.premios=[];
      this.db.readTransaction(function (t) {
        t.executeSql(sql, [], function (t, data) {
          for (var i = 0, item = null; i < data.rows.length; i++) {
             item = data.rows.item(i);
             self.premios.push(item);
           }
        });
      });
    }
  },
  computed: {

  },
  created: function() {

    this.listData();
  }
}
</script>

<style scoped="true">
  .data{
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    transform-style: preserve-3d;
    text-align: center;
  }

  .home{
    width: 50px;
    color: #ffffff;
    position: fixed;
    top: 20px;
    left: 20px;
    line-height: 20px;
    font-size: 50px;
  }

  #qrpopup {
    position:fixed;
  	z-index:9999;
  	background-color:rgba(0, 0, 0,0.8);
  	width:300px;
  	height: 300px;
    border-radius: 1em;
  	top: 50%;
  	left: 50%;
  	margin: -150px 0 0 -150px;
  }

  #qrpopup input[type=text]{
  	display: block;
  	margin: 10px auto;
  }

  #qrpopup input::placeholder{
    color:#ccc !important;
  }

  .boton{
    color: #fff;
    background: #006bb3;
  }

  #tblDiv{
      text-align: center;
  }
  table#t01{
    background: rgba(255, 255, 255, 0.5);
    color: #006bb3;
  }

  table#t01 th{
    background: #006bb3;
    color:#fff;
  }

  table#t01 td, table#t01 th{
    padding: 5px;
  }

  @media (max-width: 768px) {

  }

  @media (max-width: 468px){

  }


</style>



