<template>
<transition name="slide-fade">
  <div>
  <div class="container glass mt-4 mb-4 mt-md-2">

  <div class="row">

    <div class="col-lg-8 col-md-8 offset-lg-2 offset-md-2 col-sm-12 animated fadeIn">

      <div class="titulo text-center mt-5 mt-md-2 mb-2 ">Inscríbete!</div>
      <form v-on:submit.prevent="guardar">
        <div class="form-group">
          <input class="form-control campo" type="text" placeholder="NOMBRE" v-model="nombre" maxlength="200" required>
        </div>
        <div class="form-group">
          <input class="form-control campo" type="text" placeholder="RUT" v-model="rut" @keyup="formatRut" maxlength="12" required >
        </div>
        <div class="form-group">
          <input class="form-control campo" type="tel" placeholder="FECHA DE NACIMIENTO" v-model="fnac" pattern="[0-9]{2}/[0-9]{2}/[0-9]{4}" v-mask="'XX/XX/XXXX'" required>
        </div>
        <div class="form-group">
          <input class="form-control campo" type="tel" placeholder="TELÉFONO" v-model="telefono" v-mask="'+XX X XX XX XXXX'" required>
        </div>
        <div class="form-group">
          <input class="form-control campo" type="email" placeholder="MAIL" v-model="email" required>
        </div>
        <div class="form-group text-center">
          <button type="submit" class="btn btn-primary">siguiente</button>
        </div>
        <p class="mensaje">{{mensaje}}</p>
      </form>

    </div>
  </div>

  </div>
  <img src="../assets/images/mano_form.png" class="mano animated slideInRight">
  <img src="../assets/images/boca_form.png" class="boca animated slideInLeft">

  </div>
</transition>

</template>

<script>


import axios from 'axios';

export default {
    name: "Formulario",
    data() {
        return {
          nombre:'',
          rut:'',
          fnac:'',
          telefono:'',
          email:'',
          id_registro:0,
          mensaje:'',
          db:null
        }
    },
    methods: {
      guardar(e){
        this.error=false;
        if (this.nombre && this.rut && this.fnac && this.email && this.telefono) {
          let sql = 'INSERT INTO registros (nombre, rut,fnac, email, telefono) VALUES ("' +this.nombre+'","'+this.rut+'","'+this.fnac+'","'+this.email+'","'+this.telefono+'");';
          	console.log(sql);
            this.db.transaction(function (tx) {
                tx.executeSql(sql);
            }, function (err) {
                console.log(err.message);
            },this.redirect);
        }else{
          this.mensaje="Debes ingresar todos los campos.";
        }
      },
      redirect(){
        this.$router.push({ name: 'conteo'})
      },
      formatRut(){
        console.log(event.target.value)
        this.rut = this.formateaRut(event.target.value);
      },
      formateaRut(rut) {
        rut = this.clean(rut)

        var result = rut.slice(-4, -1) + '-' + rut.substr(rut.length - 1)
        for (var i = 4; i < rut.length; i += 3) {
          result = rut.slice(-3 - i, -i) + '.' + result
        }

        return result;
      },
      clean (rut) {
        return typeof rut === 'string'
          ? rut.replace(/^0+|[^0-9kK]+/g, '').toUpperCase()
          : ''
      },
      validateRut (rut) {
        if (typeof rut !== 'string') {
          return false
        }
        if (!/^0*(\d{1,3}(\.?\d{3})*)-?([\dkK])$/.test(rut)) {
          return false
        }

        rut = this.clean(rut)

        var t = parseInt(rut.slice(0, -1), 10)
        var m = 0
        var s = 1

        while (t > 0) {
          s = (s + (t % 10) * (9 - m++ % 6)) % 11
          t = Math.floor(t / 10)
        }

        var v = s > 0 ? '' + (s - 1) : 'K'
        return v === rut.slice(-1)
      }
    },
    computed: {
    },
    created: function() {
      this.db = openDatabase(this.$BBDD, '1.0', 'erres', 2 * 1024 * 1024);
    }
}
</script>

<style scoped="true">
    ::placeholder {
      color: white;
    }
    .form-control::-webkit-input-placeholder {
      color: #fff;
    }
  .titulo{
    font-size: 4em;
    color: #7118d7;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 2px;
    text-shadow: -3px 0 white, 0 3px white, 3px 0 white, 0 -3px white;
  }

  .mano{
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 2;
    height: 60%;
    animation-delay: 1s;
  }

  .boca{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
    height: 30%;
    animation-delay: 1s;
  }

  form .campo{
    background: none;
    color: #ffffff;
    border: none;
    border-radius: 0;
    border-bottom: 2px solid #fff;
    text-transform: uppercase;
    font-size: 1.5em;
  }

  form .campo::placeholder {
    color: #ffffff !important;
  }

  .form-control:focus{
    box-shadow: none;
  }

  .texto{
    font-size: 1.4em;
  }

  .mensaje{
    color: red;
    text-align: center;
    line-height: 2em;
    text-transform: uppercase;
  }

  @media (max-width: 768px) {
    .robok{
      height: 40%;
    }

  }

  @media (max-height: 300px) {
    .boca, .mano{
      display:none;
    }
  }

  @media (max-width: 468px){
    .titulo{
      font-size: 2em;
    }
    form .campo{
      font-size: 1.1em;
    }

  }




</style>



