<template>
<transition name="slide-fade">
  <div>
  <div class="container glass mt-4 mb-4">

  <div class="row">

    <div class="col-10 offset-1 inicio">


        <div class="logo_intro">
          <h1>DE QUé GRUPO ES<br/> ESTA CANCIÓN?</h1>
            <ul class="opciones animated fadeInDown" v-for="opcion in opciones">
              <li @click="check(opcion)">{{ opcion }}</li>
            </ul>
            <img src="../assets/images/logo_conteo.png" class="animated zoomIn"/>
        </div>

    </div>
  </div>

  </div>

  <img src="../assets/images/mano_derecha.png" class="mano animated slideInUp">
  <img src="../assets/images/robot-pregunta.png" class="robot animated slideInLeft">
  </div>
</transition>

</template>

<script>
import MY_JSON from '../assets/trivia.json';
export default {
    name: "Pregunta",
    data() {
        return {
          preguntas:MY_JSON,
          sorteado:this.$route.params.sorteado,
          opciones:null
        }
    },

    computed: {

    },
    methods:{
      check(opcion){

        if(opcion == this.correcta){
          console.log('bkn');
          this.$router.push({ name: 'premio'});
        }else{
          console.log('longi ql larry');
          this.$router.push({ name: 'perdio'});
        }
      },
      shuffle(arra1) {
          let ctr = arra1.length;
          let temp;
          let index;
          while (ctr > 0) {
          // Pick a random index
              index = Math.floor(Math.random() * ctr);
              // Decrease ctr by 1
              ctr--;
              // And swap the last element with it
              temp = arra1[ctr];
              arra1[ctr] = arra1[index];
              arra1[index] = temp;
          }
          return arra1;
      }
    },

    created: function() {
      this.opciones=this.shuffle(this.preguntas[this.sorteado].opcion);
      this.correcta=this.preguntas[this.sorteado].correcta;
    }
}
</script>

<style scoped="true">
  .inicio{
    height: 90vh;
    display: table;
    background:url('../assets/images/bkg_pregunta.png') no-repeat;
    background-size: 50% auto;
    background-position: center 70%;
  }

  h1{
    font-size: 3em;
    color: #0075c9;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 2px;
    text-shadow: -3px 0 white, 0 3px white, 3px 0 white, 0 -3px white;
  }

  .logo_intro{
    width: 50%;
    margin: 0 auto;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }

  .logo_intro img{
    width: 50%;
    animation-delay: 1s;
  }

  .mano{
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 2;
    height: 65%;
    animation-delay: 1.5s;
  }

  .robot{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
    height: 65%;
    animation-delay: 1.5s;
  }

  ul.opciones{
    margin: 25px auto;
    width:60%;
  }

  ul.opciones li{
    list-style: none;
    margin: 10px 0 !important;
    padding: 5px 0;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 2px;
    font-size: 1.8em;
    background:#611ed2;
    border-radius: 5px;
  }

  @media (max-width: 768px) {
    .mano{
      height: 40%;
    }
    .botellas{
      height: 50%;
    }
  }

  @media (max-width: 468px){
    .logo_intro{
      width: 80%;
      margin: 0px auto 50px auto;
    }
    .mano{
      height: 30%;
    }
    .botellas{
      display: none;
    }
    .texto-mayor{
      font-size: 2em;
    }
  }


</style>



