<template>
<transition name="slide-fade">
  <div>
  <div class="container glass mt-4 mb-4">

  <div class="row">

    <div class="col-lg-8 col-sm-12 offset-lg-2 mt-lg-2 zoomIn animated">
      <div class="row mb-2 p-5">
        <img src="../assets/images/logo_conteo.png" class="img-fluid ">
      </div>
    </div>

    <div class="col-lg-10 col-sm-12 offset-lg-1 mt-lg-4 zoomIn animated">

        <div class="row mt-3 mb-3">
          <div class="col-12 conteo"><router-link to="/">Felicitaciones!</router-link></div>
        </div>



        <div class="row">
          <div class="col-12 text-center mt-3 mb-2 ponte_vio"> Ganaste: </div>
        </div>

        <h1 class="text-center animated infinite pulse">{{ganador.name}}</h1>

    </div>


  </div>

  </div>
  <img src="../assets/images/joyas_premio.png" class="robot-izq animated slideInLeft">
  <img src="../assets/images/robot_premio.png" class="robot-der animated slideInRight">
  </div>
</transition>

</template>

<script>


export default {
    name: "Premio",
    data() {
        return {
          ganador:null,
          db:null,
          premios:[]
        }
    },
    methods: {
      getPremios(){
        this.db = openDatabase(this.$BBDD, '1.0', 'erres', 2 * 1024 * 1024);
        let sql = "SELECT * FROM premios WHERE number > 0";
        let self = this;
        this.premios=[];
        this.db.readTransaction(function (t) {
          t.executeSql(sql, [], function (t, data) {
            for (var i = 0, item = null; i < data.rows.length; i++) {
               item = data.rows.item(i);
               self.premios.push(item);
             }
             self.getGanador();
          });
        });
      },
      getGanador(){
        let sel = Math.floor(Math.random()*this.premios.length);
        console.log(sel);
        this.ganador = this.premios[sel];
        let self = this;
        let sql = `UPDATE premios set number= number -1 WHERE id = ${this.ganador.id}`;
        if(this.ganador.number>0){
          this.db.transaction(function (tx) {
              console.log(sql);
              tx.executeSql(sql);
          }, function (err) {
              console.log(err.message);
          },console.log('ok'));
        }
      }
    },

    created: function() {
      this.getPremios();

    }
}
</script>

<style scoped="true">

  .conteo{
    color: #ff4438;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 3px;
    font-size: 4em;
    line-height: 1em;
    text-align: center;
    text-shadow: -9px 0 white, 0 9px white, 9px 0 white, 0 -9px white;
  }

  .conteo a{
    color: #ff4438;
    text-decoration: none !important;
  }

  .ponte_vio{
    color: #00af9a;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 3px;
    font-size: 3em;
    line-height: 1em;
    text-shadow: -5px 0 white, 0 5px white, 5px 0 white, 0 -5px white;
  }

  h1{
    margin: 50px 0;
    font-size: 1.2em;
    color: #00af9a;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 3px;
    font-size: 3.4em;
    line-height: 1em;
    text-shadow: -5px 0 white, 0 5px white, 5px 0 white, 0 -5px white;
  }

  .desc{
    font-size: 1.4em;
    line-height: 1.1em;
  }

  img.lolla-lounge{
    width: 60px;
    height: auto;
  }

  .robot-der{
    position: fixed;
    bottom: 0px;
    right: 0px;
    z-index: 2;
    width:auto;
    height: 55%;
    animation-delay: 1s;
  }

  .robot-izq{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
    height: 40%;
    animation-delay: 1s;
  }

  @media (max-width: 768px) {
    .mano{
      height: 40%;
    }
    .boca{
       height: 30%;
    }
  }

  @media (max-width: 468px){
    .participando{
      font-size: 2em;
    }
    .boca, .mano{
      display: none;
    }
  }


</style>



