<template>
<transition name="slide-fade">
  <div>
    <div class="container glass mt-4 mb-4">

    <div class="row">

      <div class="col-12">
        <div class="data">
          <router-link to='/' class="home">⌂</router-link>
          <p>
          <h1>Hay <span>{{ total }}</span> registros para sincronizar.</h1>
            <button @click="saveData()" :disabled="total == 0" class="btn">Subir registros &#x21EA;</button>
            <template v-if="loading">
              <div class="lds-dual-ring" v-if="total>0"></div>
            </template>
          </p>
        </div>

      </div>
    </div>

    </div>

  </div>
</transition>

</template>

<script>
import axios from 'axios';
export default {
  name: 'Datos',
  props: {
    msg: String
  },
  data(){
    return{
      db:null,
      total:0,
      loading:false,
      upload:false,
      api_url:'https://script.google.com/macros/s/AKfycbwE34UVXm1NwgHHI1sF_7__XVYjcYB44RQPi1tdfFeV4QW4Sqk/exec'
    }
  },
  methods:{
    getTotal: function(){
      this.db = openDatabase(this.$BBDD, '1.0', 'tres_erres', 2 * 1024 * 1024);
      var ref = this;
      this.db.transaction(function (tx) {
          tx.executeSql('SELECT COUNT(*) AS c FROM registros', [], function (tx, r) {
            ref.total = r.rows.item(0).c;
          }, function (tx, e) {
            //alert ("unknown: " + e.message);
          });
      });
    },
    saveData:function(){
      this.loading=true;
      var ref = this;
      this.db.transaction(function (tx) {
          tx.executeSql('SELECT * FROM registros', [], function (tx, r) {
              console.log(r.rows);
              var largo =r.rows.length;
              var i =0;
              for(i=0;i<largo;i++){
                  var datos = {
                    'id':r.rows.item(i).id,
                    'nombre':r.rows.item(i).nombre,
                    'rut':r.rows.item(i).rut,
                    'fecha de nacimiento':r.rows.item(i).fnac,
                    'email':r.rows.item(i).email,
                    'telefono':r.rows.item(i).telefono
                  };
                  ref.guardaRegistro(datos);
              }
              ref.getTotal();
          }, function (tx, e) {
            //alert ("unknown: " + e.message);
          });
      });
    },
    guardaRegistro(datos){
      var api = this.api_url;
      var id = datos.id
      var ref = this;
      axios({
        method: 'get',
        url: api,
        params: datos,
        headers: {
            'Content-Type': 'text/plain;charset=utf-8',
        },
      }).then((response) => {
        console.log(response);
        ref.borraRegistro(id);
      }, (error) => {
        console.log(error);
      });
    },
    borraRegistro(id){
      var ref = this;
      console.log(id);
      this.db.transaction(function(tx) {
          tx.executeSql('delete from registros where id='+id, [], function(transaction, result) {
              console.info('Record Deleted Successfully!');
              if(ref.total>0){
                  ref.total = ref.total -1;
              }
          }, function(transaction, error) {
              console.log(error);
          });
      });
    }
  },
  created: function() {
    if(!navigator.onLine) {
    	 alert('Debes estar conectado para realizar la sincronización de datos');
    }
    this.getTotal();
  }
}
</script>

<style scoped="true">
  .data{
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    transform-style: preserve-3d;
    text-align: center;
  }

  .lds-dual-ring {
    margin: 30px auto;
    width: 64px;
    height: 64px;
  }
  .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 46px;
    height: 46px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid #fff;
    border-color: #fff transparent #fff transparent;
    animation: lds-dual-ring 1.2s linear infinite;
  }
  .home{
    width: 50px;
    color: #ffffff;
    position: fixed;
    top: 20px;
    left: 20px;
    line-height: 20px;
    font-size: 50px;
  }

  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  @media (max-width: 768px) {

  }

  @media (max-width: 468px){

  }


</style>



