<template>
<transition name="slide-fade">
  <div>
  <div class="container glass mt-4 mb-4">

  <div class="row">

    <div class="col-4 offset-4 mt-5 mt-md-2">
      <img src="../assets/images/logo_promo.png" class="img-fluid"/>
    </div>

    <div class="col-12">
      <audio :src="mp3" controls id="player" hidden></audio>
      <div class="audio text-center animated zoomIn" >
        <svg @click="escuchar" id="wave" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 38.05">
          <path id="Line_1" data-name="Line 1" d="M0.91,15L0.78,15A1,1,0,0,0,0,16v6a1,1,0,1,0,2,0s0,0,0,0V16a1,1,0,0,0-1-1H0.91Z"/>
          <path id="Line_2" data-name="Line 2" d="M6.91,9L6.78,9A1,1,0,0,0,6,10V28a1,1,0,1,0,2,0s0,0,0,0V10A1,1,0,0,0,7,9H6.91Z"/>
          <path id="Line_3" data-name="Line 3" d="M12.91,0L12.78,0A1,1,0,0,0,12,1V37a1,1,0,1,0,2,0s0,0,0,0V1a1,1,0,0,0-1-1H12.91Z"/>
          <path id="Line_4" data-name="Line 4" d="M18.91,10l-0.12,0A1,1,0,0,0,18,11V27a1,1,0,1,0,2,0s0,0,0,0V11a1,1,0,0,0-1-1H18.91Z"/>
          <path id="Line_5" data-name="Line 5" d="M24.91,15l-0.12,0A1,1,0,0,0,24,16v6a1,1,0,0,0,2,0s0,0,0,0V16a1,1,0,0,0-1-1H24.91Z"/>
          <path id="Line_6" data-name="Line 6" d="M30.91,10l-0.12,0A1,1,0,0,0,30,11V27a1,1,0,1,0,2,0s0,0,0,0V11a1,1,0,0,0-1-1H30.91Z"/>
          <path id="Line_7" data-name="Line 7" d="M36.91,0L36.78,0A1,1,0,0,0,36,1V37a1,1,0,1,0,2,0s0,0,0,0V1a1,1,0,0,0-1-1H36.91Z"/>
          <path id="Line_8" data-name="Line 8" d="M42.91,9L42.78,9A1,1,0,0,0,42,10V28a1,1,0,1,0,2,0s0,0,0,0V10a1,1,0,0,0-1-1H42.91Z"/>
          <path id="Line_9" data-name="Line 9" d="M48.91,15l-0.12,0A1,1,0,0,0,48,16v6a1,1,0,1,0,2,0s0,0,0,0V16a1,1,0,0,0-1-1H48.91Z"/>
        </svg>
      </div>
      <p class="text-center" v-if="btnPlay">
        <button @click="escuchar" class="btn">play</button>
      </p>

    </div>
  </div>

  </div>
  <img src="../assets/images/robot-izquierda.png" class="robot-izq animated slideInLeft">
  <img src="../assets/images/mano_derecha.png" class="mano animated slideInRight">
  <img src="../assets/images/boca.png" class="boca animated slideInRight">
  </div>
</transition>

</template>

<script>
import MY_JSON from '../assets/trivia.json';

export default {
    name: "Cancion",
    data() {
        return {
          largo:0,
          preguntas:MY_JSON,
          mp3:null,
          btnPlay:true,
          sorteado:null
        }
    },
    methods: {
        playMusic(){
            let player = document.getElementById("player");
            player.play();
        },
        escuchar(){
          let self = this;
          let player = document.getElementById("player");
          let wrapper = document.getElementById("wave");

          if (player.paused){
            player.play();
            wrapper.classList.add("animacion");
            this.btnPlay=false;
            player.onended = function() {
                self.$router.push({ name: 'pregunta', params: {sorteado: self.sorteado }});
            };
          }else{
            player.pause();
            wrapper.classList.remove("animacion");
            this.btnPlay=true;
          }
        }
    },
    computed: {

    },

    created: function() {
        let largo = this.preguntas.length;
        let sorteado = Math.floor(Math.random() * largo) + 1
        console.log(this.preguntas[sorteado-1]);
        this.sorteado = sorteado-1;
        this.mp3="file:///android_asset/www/dist/mp3/"+this.preguntas[sorteado-1].mp3;
        this.playMusic();
    }
}
</script>

<style scoped="true">
  .audio{
    margin: 50px auto 30px auto;
    height: 320px;
    width: 70%;
    position: relative;
    z-index: 2;
  }

  .robot-izq{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
    height: 70%;
    animation-delay: 1s;
  }

  .mano{
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 2;
    height: 70%;
    animation-delay: 1s;
  }

  .boca{
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 2;
    height: 30%;
    animation-delay: 1s;
  }

  #wave {
    height: 100%;
    width: 100%;
    fill: #7118d7;
    stroke:#fff;
    stroke-width: 1px;
  }


  #wave.animacion #Line_1 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 0.15s;
            animation-delay: 0.15s;
  }

  #wave.animacion #Line_2 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
  }

  #wave.animacion #Line_3 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 0.45s;
            animation-delay: 0.45s;
  }

  #wave.animacion #Line_4 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
  }

  #wave.animacion #Line_5 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 0.75s;
            animation-delay: 0.75s;
  }

  #wave.animacion #Line_6 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 0.9s;
            animation-delay: 0.9s;
  }

  #wave.animacion #Line_7 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 1.05s;
            animation-delay: 1.05s;
  }

  #wave.animacion #Line_8 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
  }

  #wave.animacion #Line_9 {
    -webkit-animation: pulse 1s infinite;
            animation: pulse 1s infinite;
    -webkit-animation-delay: 1.35s;
            animation-delay: 1.35s;
  }


@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  50% {
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  50% {
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}


  @media (max-width: 768px) {

  }

  @media (max-width: 468px){
    .video{
      width: 100%;
      margin-top: 30px;
    }

    .video .left, .video .right{
      display: none;
    }
  }


</style>



