<template>
<transition name="slide-fade">
  <div>
  <div class="container glass mt-4 mb-4">

  <div class="row">


    <div class="col-lg-8 col-md-8 col-sm-12 offset-lg-2 offset-md-2 mt-lg-4 zoomIn animated">

        <div class="row mt-5 mb-3">
          <img src="../assets/images/logo_conteo.png" class="img-fluid">
        </div>

        <div class="row mt-3 mb-3">
          <div class="col-12 conteo">{{numero}}</div>
        </div>

        <div class="row">
          <div class="col-12 text-center mt-3 mb-2 ponte_vio">
            ponte los<br/>audifonos
          </div>
        </div>

    </div>


  </div>

  </div>
  <img src="../assets/images/robot-izquierda.png" class="robot-izq animated slideInLeft">
  <img src="../assets/images/robot-derecha.png" class="robot-der animated slideInRight">
  </div>
</transition>

</template>

<script>


export default {
    name: "Conteo",
    data() {
        return {
          numero:10
        }
    },
    methods: {
        redirect(){
            this.$router.push('/cancion');
        }
    },
    computed: {

    },
    created: function() {

      var self = this;
      var interval = setInterval(function() {
          if (self.numero>0) {
               self.numero--;
          }else{
            clearInterval(interval);
            self.redirect();
          }
      }, 1000);

    }
}
</script>

<style scoped="true">

  .conteo{
    color: #0075c9;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 3px;
    font-size: 14em;
    line-height: 1em;
    text-align: center;
    text-shadow: -9px 0 white, 0 9px white, 9px 0 white, 0 -9px white;
  }

  .ponte_vio{
    color: #7118d7;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 3px;
    font-size: 3em;
    line-height: 1em;
    text-shadow: -5px 0 white, 0 5px white, 5px 0 white, 0 -5px white;
  }



  .desc{
    font-size: 1.4em;
    line-height: 1.1em;
  }

  img.lolla-lounge{
    width: 60px;
    height: auto;
  }

  .robot-der{
    position: fixed;
    bottom: 0px;
    right: 0px;
    z-index: 2;
    width:auto;
    height: 70%;
    animation-delay: 1s;
  }

  .robot-izq{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
    height: 70%;
    animation-delay: 1s;
  }

  @media (max-width: 768px) {
    .mano{
      height: 40%;
    }
    .boca{
       height: 30%;
    }
  }

  @media (max-width: 468px){
    .participando{
      font-size: 2em;
    }
    .boca, .mano{
      display: none;
    }
  }


</style>



