<template>
<transition name="slide-fade">
  <div>
  <div class="container glass mt-4 mb-4">

  <div class="row">

    <div class="col-12 inicio">
        <div class="logo_intro">
          <router-link to="/formulario"><img src="../assets/images/logo_promo.png" class="animated zoomIn"/></router-link>
        </div>

    </div>
  </div>

  </div>
  <div class="setup">
    <table>
      <tr>
        <td><router-link to="/datos"><img src="../assets/images/nube.png" ></router-link></td>
        <td>&nbsp;</td>
        <td><router-link to="/setup"><img src="../assets/images/setup.png" ></router-link></td>
      </tr>
    </table>


  </div>

  <img src="../assets/images/boca-arriba.png" class="boca animated slideInDown">
  <img src="../assets/images/diamantes.png" class="diamantes animated slideInDown">
  <img src="../assets/images/mano-home.png" class="mano animated slideInUp">
  <img src="../assets/images/robot-derecha.png" class="robot animated slideInRight">
  </div>
</transition>

</template>

<script>
export default {
    name: "Home",
    data() {
        return {
          db:null,
          total:0
        }
    },

    methods: {
      hayPremios(){
        let sql = "SELECT SUM(number) as total FROM premios";
        let self = this;
        this.premios=[];
        this.db.readTransaction(function (t) {
          t.executeSql(sql, [], function (t, data) {
            console.log(data.rows[0].total);
            if((data.rows[0].total==0)||(data.rows[0].total==null)){
              alert('No hay premios ingresados, ingresa algunos antes de comenzar');
            }
          });
        });
      }
    },

    created: function() {
      this.db = openDatabase(this.$BBDD, '1.0', 'erres', 2 * 1024 * 1024);
      this.db.transaction(function (tx) {
          tx.executeSql('CREATE TABLE IF NOT EXISTS registros (id INTEGER PRIMARY KEY AUTOINCREMENT, nombre,rut,fnac,email,telefono)');
          tx.executeSql('CREATE TABLE IF NOT EXISTS premios (id INTEGER PRIMARY KEY AUTOINCREMENT, name, number)');
      }, function (err) {
          console.log(err.message);
      });
      this.hayPremios();
    }
}
</script>

<style scoped="true">
  .inicio{
    height: 90vh;
    display: table;
  }
  .logo_intro{
    width: 50%;
    margin: 0 auto;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }

  .logo_intro img{
    width: 50%;
    animation-delay: 1s;
  }
  .mano{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 2;
    height: 60%;
    animation-delay: 1.5s;
  }

  .robot{
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 2;
    height: 60%;
    animation-delay: 1.5s;
  }

  .boca{
    position: fixed;
    top: 0;
    right: 0;
    z-index: 2;
    height: 30%;
    animation-delay: 1.5s;
  }

  .diamantes{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    height: 30%;
    animation-delay: 1.5s;
  }

  .texto{
    font-size: 1.4em;
  }
  .texto-mayor{
    color: #006bb3;
    font-size: 3em;
    line-height: 1em;
    text-transform: uppercase;
    font-family: "Ovink";
    letter-spacing: 2px;
    text-shadow: -3px 0 white, 0 3px white, 3px 0 white, 0 -3px white;
  }

  .btn.mayor{
    width: 80px;
    font-size: 2em;
    margin: 0 5px;
  }

  .setup{
    position: fixed;
    bottom: 30px;
    width: 120px;
    left:50%;
    z-index: 2;
    margin-left: -50px;
  }
  .setup table{
    width: 100%;
    text-align: center;
  }

  @media (max-width: 768px) {
    .mano{
      height: 40%;
    }
    .botellas{
      height: 50%;
    }
  }

  @media (max-width: 468px){
    .logo_intro{
      width: 80%;
      margin: 0px auto 50px auto;
    }
    .mano{
      height: 30%;
    }
    .botellas{
      display: none;
    }
    .texto-mayor{
      font-size: 2em;
    }
  }


</style>



