import Home from "../components/Home"
import Cancion from '../components/Cancion.vue'
import Formulario from '../components/Formulario.vue'
import Conteo from '../components/Conteo.vue'
import Pregunta from '../components/Pregunta.vue'
import Datos from '../components/Datos.vue'
import SetUp from '../components/SetUp.vue'
import Premio from '../components/Premio.vue'
import Perdio from '../components/Perdio.vue'

const routes= [
      { path: '/', component: Home },
      { path: '/inicio', name:'inicio', component: Home },
      { path: '/formulario', component: Formulario },
      { path: '/conteo', name:"conteo", component: Conteo, props: true },
      { path: '/pregunta', name:"pregunta", component: Pregunta, props: true },
      { path: '/cancion', name:"cancion", component: Cancion },
      { path: '/datos', name:"datos", component: Datos },
      { path: '/setup', name:"setup", component: SetUp },
      { path: '/premio', name:"premio", component: Premio },
      { path: '/perdio', name:"perdio", component: Perdio }
    ]
export default routes;



